<template>
  <div>
    <b-row>
      <b-col>
        <b-button
          variant="primary"
          v-b-toggle:formCollapse
          class="mt-1 actionButton"
          v-show="!formStateCollapse"
        >
          <i class="fas fa-plus-circle"></i> New {{ type }}
        </b-button>
      </b-col>
      <b-col cols="10">
        <b-input-group class="mt-1" v-show="!formStateCollapse">
          <b-form-input
            v-model="filterSearch"
            placeholder="Search..."
            class="ml-1 mr-1 shadow-sm"
            type="search"
          ></b-form-input>
          <b-pagination
            class="ml-2 mr-2 shadow-sm"
            v-model="currentPage"
            :total-rows="rows"
            :per-page="perPage"
            aria-controls="mainTable"
          ></b-pagination>
          <b-form @submit.prevent="getData()">
            <b-button variant="primary" type="submit" class="actionButton"
              ><i class="fas fa-sync"></i
            ></b-button>
          </b-form>
        </b-input-group>
      </b-col>
      <!-- Main Form ------------------------------------------------------------>
      <component
        :is="current"
        ref="component"
        @hook:mounted="getData()"
      ></component>
      <!-- Results -------------------------------------------------------------->
    </b-row>
    <div class="mt-2 overflow-auto" v-show="!formStateCollapse">
      <b-table
        class="shadow-sm"
        style="font-size:90%;"
        responsive
        id="mainTable"
        :busy="busy"
        :filter="filterSearch"
        :total-rows="rows"
        :fields="mainTableFields"
        :items="items"
        :per-page="perPage"
        :current-page="currentPage"
        hover
        small
      >
        <template #table-busy>
          <div class="text-center text-danger my-2">
            <b-spinner class="align-middle"></b-spinner>
          </div>
        </template>
        <template #cell(action)="row" align="center" style="padding: 0px 0px 0px 0px; margin:0px 0px 0px 0px" >
          <b-button-group align="center" size="sm" style="padding: 0px 0px 0px 0px; margin:0px 0px 0px 0px" >
            <b-button
              size="sm"
              variant="outline-info"
              @click="row.toggleDetails"
              class="actionButton ml-1"
              ><i class="fas fa-info"></i>
              {{ row.detailsShowing ? '' : '' }}
            </b-button>
            <b-button
              size="sm"
              variant="outline-success"
              v-b-toggle:formCollapse
              @click="editComponent(row.item)"
              class="actionButton ml-1"
              ><i class="fas fa-edit fa-2x"></i
            ></b-button>
            
            <b-button
              size="sm"
              v-if="type=='job'"
              variant="outline-info"
              @click="actionComponent(row.item)"
              class="actionButton ml-1"
              ><i class="fas fa-window-close fa-2x"></i
            ></b-button>
            <b-button
              size="sm"
              variant="outline-danger"
              @click="deleteData(row.item)"
              class="actionButton ml-1"
              ><i class="fas fa-trash-alt fa-2x"></i
            ></b-button>
          </b-button-group>
        </template>
        <template #row-details="row">
          <b-card>
            <vue-json-pretty
              :showDoubleQuotes="false"
              :showIcon="true"
              :data="row.item"
            />
          </b-card>
        </template>
      </b-table>
    </div>
  </div>
</template>

<script>
import EreportService from '../services/ereport.service';
import AuthService from '../services/auth.service';
import VueJsonPretty from 'vue-json-pretty';
import 'vue-json-pretty/lib/styles.css';

export default {
  name: 'Crud',
  props: ['current', 'mainTableFields', 'type'],
  data() {
    return {
      busy: false,
      perPage: 10,
      currentPage: 1,
      formStateCollapse: false,
      filterSearch: '',
      items: [],
      deleteConfirm: '',
      options: ['list', 'of', 'options'],
      value:''
    };
  },
  methods: {
    getData() {
      this.busy = true;

      switch (this.type) {
        case 'user':
          AuthService.getUsers()
            .then(data => {
              this.items = data;
            })
            .catch(error => this.$parent.catchError(error));
          break;

        case 'job':
          var sites = '';
          for(var i=0;i < this.$store.state.auth.user.attributes.length;i++)
          {
            if(this.$store.state.auth.user.attributes[i].code == 'site'){
              sites = sites + ',' + this.$store.state.auth.user.attributes[i].value;
            }
          }
          EreportService.getDataByAux(this.type,sites.substring(1))
            .then(data => {
              this.items = data;
            })
            .catch(error => this.$parent.catchError(error));
          break;

        default:
          EreportService.getData(this.type)
            .then(data => {
              this.items = data;
            })
            .catch(error => this.$parent.catchError(error));
      }

      this.busy = false;
    },
    getDataFromType(type) {
      return EreportService.getData(type);
    },
    saveData(data) {
      this.busy = true;

      switch (this.type) {
        case 'user':
          AuthService.saveUser(data).then(data => {
            this.showMessage(data.message);
            this.getData();
          });
          break;

        default:
          EreportService.saveData(this.type, data)
            .then(data => {
              this.showMessage(data.message);
              this.getData();
            })
            .catch(error => this.$parent.catchError(error));
      }

      this.busy = false;
    },
    updateData(data) {
      this.busy = true;

      switch (this.type) {
        case 'user':
          AuthService.updateUser(data)
            .then(data => {
              this.showMessage(data.message);
              this.getData();
            })
            .catch(error => this.$parent.catchError(error));
          break;

        default:
          EreportService.updateData(this.type, data)
            .then(data => {
              this.showMessage(data.message);
              this.getData();
            })
            .catch(error => this.$parent.catchError(error));
      }

      this.busy = false;
    },
    deleteData(data) {
      this.deleteConfirm = '';
      this.$bvModal
        .msgBoxConfirm('Are you sure?', {
          title: '',
          okVariant: 'danger',
          okTitle: 'YES',
          cancelTitle: 'NO',
          footerClass: 'p-2',
          hideHeaderClose: false,
          centered: true
        })
        .then(value => {
          this.deleteConfirm = value;
          if (this.deleteConfirm == true) {
            switch (this.type) {
              case 'user':
                AuthService.deleteUser(data.id)
                  .then(data => {
                    this.showMessage(data.message);
                    this.getData();
                  })
                  .catch(error => this.$parent.catchError(error));
                break;

              default:
                EreportService.deleteData(this.type, data.id)
                  .then(data => {
                    this.showMessage(data.message);
                    this.getData();
                  })
                  .catch(error => this.$parent.catchError(error));
            }
          }
        });
    },
    showMessage(message) {
      this.$parent.showMsgBoxConfirm(message, 'success', 'Success', 'sm');
    },
    editComponent(item) {
      this.$refs.component.loadItem(item);
    },
    actionComponent(item)
    {
      if(this.type == 'job')
      {
        this.$refs.component.closeJob(item);
      }
    },
    catchError(error) {
      this.$parent.catchError(error);
    }
  },
  computed: {
    currentUser() {
      return this.$store.state.auth.user;
    },
    rows() {
      return this.items.length;
    }
  },
  mounted() {
    if (!this.currentUser) {
      this.$router.push('/login');
    }
    this.$root.$on('bv::collapse::state', (collapseId, isJustShown) => {
      if (collapseId == 'formCollapse') {
        this.formStateCollapse = isJustShown;
      }
    });
  },
  components: {
    "job-dynamic": () => import("./JobComponent.vue"),
    "customer-dynamic": () => import("./CustomerComponent.vue"),
    "site-dynamic": () => import("./SiteComponent.vue"),
    "user-dynamic": () => import("./UserComponent.vue"),
    VueJsonPretty
}
};
</script>
