import axios from 'axios';
require('dotenv').config();

class EreportService {

  getData(type) {
    let user = JSON.parse(localStorage.getItem('user'));
    return axios
      .get(process.env.VUE_APP_EREPORT_SERVICE +'/api/ereport/' + type, {
        headers: {
          Authorization: 'Bearer ' + user.accessToken
        }
      }).then(response => response.data)
  }

  getDataById(type,id) {
    let user = JSON.parse(localStorage.getItem('user'));
    return axios
      .get(process.env.VUE_APP_EREPORT_SERVICE +'/api/ereport/' + type + '/'+id, {
        headers: {
          Authorization: 'Bearer ' + user.accessToken
        }
      }).then(response => response.data)
  }

  getDataByAux(type,aux) {
    let user = JSON.parse(localStorage.getItem('user'));
    return axios
      .get(process.env.VUE_APP_EREPORT_SERVICE +'/api/ereport/' + type + '/'+aux, {
        headers: {
          Authorization: 'Bearer ' + user.accessToken
        }
      }).then(response => response.data)
  }

  saveData(type,formData) {
    let user = JSON.parse(localStorage.getItem('user'));
    return axios
      .post(
        process.env.VUE_APP_EREPORT_SERVICE +'/api/ereport/'+ type,
         formData,
        {
          headers: {
            Authorization: 'Bearer ' + user.accessToken
          }
        }
      )
      .then(response => response.data)
  }

  updateData(type,formData) {
    let user = JSON.parse(localStorage.getItem('user'));
    return axios
      .put(
        process.env.VUE_APP_EREPORT_SERVICE +'/api/ereport/'+ type,
         formData,
        {
          headers: {
            Authorization: 'Bearer ' + user.accessToken
          }
        }
      )
      .then(response => response.data)
  }

  deleteData(type,formData) {
    let user = JSON.parse(localStorage.getItem('user'));
    return axios
      .delete(
        process.env.VUE_APP_EREPORT_SERVICE +'/api/ereport/'+ type +'/'+formData,
        {
          headers: {
            Authorization: 'Bearer ' + user.accessToken
          }
        }
      )
      .then(response => response.data)
  }
 

}

export default new EreportService();